import React, { FC } from 'react';
import Section from './section';

interface Project {
  name: string;
  background: string;
  logo: string;
  description: string;
  keywords: string[];
}

interface Props {
  data: Project[];
}

const Card: FC<Project> = ({ name, background, logo, description, keywords }) => (
  <div className="flex items-center sm:block rounded shadow overflow-hidden transition duration-200 ease-in-out hover:shadow-xl hover:scale-105 mb-8 sm:mb-0">
    <div
      className="w-10 h-10 rounded-full mx-4 lg:w-full lg:h-64 lg:rounded-none lg:mx-0 flex justify-center items-center"
      style={{ background }}
    >
      <img src={logo} alt="project-logo" className="hidden lg:block mx-auto w-3/6 rounded-full" />
    </div>
    <div className="px-6 pt-4 pb-2 flex-grow-0">
      <div className="font-bold text-gray-900 text-sm lg:text-base lg:mb-2">{name}</div>
      <div className="hidden lg:block text-gray-700 text-base mb-4">{description}</div>
      {keywords && keywords.length ? (
        <div className="flex flex-wrap justify-start -mx-2">
          {keywords.map((keyword) => (
            <div
              key={keyword}
              className="inline-block lg:bg-gray-200 rounded-full lg:px-3 lg:py-1 text-xs font-medium text-gray-800 m-1"
            >
              {keyword}
            </div>
          ))}
        </div>
      ) : null}
    </div>
  </div>
);

const Portfolio: FC<Props> = ({ data }) => (
  <Section title="My Portfolio">
    <div className="sm:grid sm:grid-cols-2 xl:grid-cols-3 col-gap-4 row-gap-8">
      {data.map((project) => (
        <Card key={project.name} {...project} />
      ))}
    </div>
  </Section>
);

export default Portfolio;
